// main: theme.bootstrap

body {
	margin: 0;
	padding: 0;
	font: 100% $default-font-name;
}

html {
	height: 100%;
}

* {
	box-sizing: border-box;

	&:before {
		box-sizing: border-box;
	}

	&:after {
		box-sizing: border-box;
	}
}

a,
abbr,
b,
em,
h1,
h2,
h3,
h4,
h5,
h6,
i,
img,
menu,
nav,
p,
u {
	margin: 0;
	padding: 0;
	font-weight: $default-font-weight-headlines;
	@include show-hide($reset-links-theme) {
		border: 0;
		font-size: 100%;
		font: inherit;
	};
}

a {
	text-decoration: none;
	color: $default-link-color;
	font: inherit;
	font-size: $default-link-font-size;

	&:link {
		color: $default-link-color;
	}

	&:hover {
		color: $default-link-color-hover;
	}
}

ol,
ul {
	margin: 0;
	padding: 0;

	li {
		list-style: none;
	}
}
